
import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Package } from './package.model';

@Component({
  selector: 'app-package-list',
  template: `
    <mdc-card class="demo-card" outlined>
      <ng-container *ngFor="let pkg of pkgs">
        <mdc-ripple class="demo-card-article"
          (click)="pkgClick.emit(pkg)">
          <app-package-header [pkg]="pkg"
            (githubClick)="githubClick.emit({ event: $event, pkg: pkg })"
            (homepageClick)="homepageClick.emit({ event: $event, pkg: pkg })">
          </app-package-header>
        </mdc-ripple>
        <mdc-list-divider></mdc-list-divider>
      </ng-container>
    </mdc-card>
  `
})

export class PackageListComponent implements OnInit {

  @Input() pkgs: Package[];

  @Output() pkgClick = new EventEmitter<Package>();
  @Output() githubClick = new EventEmitter<{ event: MouseEvent, pkg: Package }>();
  @Output() homepageClick = new EventEmitter<{ event: MouseEvent, pkg: Package }>();

  constructor() { }

  ngOnInit() { }
}
